
ul,
li {
  list-style: none;
  padding-left: 0;
}

.stageWrapper {
  width: 600px;
  margin: 10px auto;

  .stageRowList {
    display: flex;
    align-items: center;
    border-top: 1px solid #000;
    text-align: center;

    &:last-child {
      border-bottom: 1px solid #000;
    }
    .stageRowItem {
      width: 100px;
      height: 100px;
      line-height: 100px;
      flex: 1 0 auto;
      border-left: 1px solid #000;
      box-sizing: border-box;

      &:last-child {
        border-right: 1px solid #000;
      }
      &.selected {
        color: #f00;
        font-weight: bold;
      }
    }
  }
}
